import React, { useState } from 'react';
import axios from 'axios';
import { useNavigate, Link } from 'react-router-dom';
import Header from './Header';
import '../css/header.css';
import '../css/Login.css';
import DummyFooter from './DummyFooter';

const Login = ({ setIsLoggedIn }) => {
    const [loginId, setLoginId] = useState('');
    const [pw, setPw] = useState('');
    const [error, setError] = useState('');
    const navigate = useNavigate();

    const handleSubmit = async (event) => {
        event.preventDefault();

        try {
            const response = await axios.post('/api/users/login', null, { params: { loginId, pw } });

            if (response.data) {
                console.log(' :', response.data);

                //     
                sessionStorage.setItem('loginId', response.data.loginId);
                sessionStorage.setItem('pw', response.data.pw);
                sessionStorage.setItem('email', response.data.email);
                sessionStorage.setItem('tel', response.data.tel);
                sessionStorage.setItem('userNo', response.data.userNo);
                setIsLoggedIn(true);
                navigate('/');
            } else {
                setError('    .');
                navigate('/login');
            }
        } catch (error) {
            console.error('  :', error);
            setError('    .');
        }
    };

    return (
        <div>
            <Header />
            <div className="login-container">
                <h2></h2>
                {error && <div>{error}</div>}
                <form onSubmit={handleSubmit}>
                    <div>
                        <label htmlFor="loginId">:</label>
                        <input
                            type="text"
                            id="loginId"
                            name="loginId"
                            value={loginId}
                            onChange={(e) => setLoginId(e.target.value)}
                        />
                    </div>
                    <div>
                        <label htmlFor="pw">:</label>
                        <input
                            type="password"
                            id="pw"
                            name="pw"
                            value={pw}
                            onChange={(e) => setPw(e.target.value)}
                        />
                    </div>
                    <button type="submit"></button>
                </form>
                <Link to="/signup" className="signup-link"></Link>
                <Link to="/" className="home-link"> </Link>
            </div>
           <DummyFooter />
        </div>
    );
};

export default Login;
