
import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

function MyPlan() {
  const [loginId, setLoginId] = useState('');
    const [pw, setPw] = useState('');
    const [email, setEmail] = useState('');
    const [tel, setTel] = useState('');

    useEffect(() => {
      //    
      const storedLoginId = sessionStorage.getItem('loginId');
      const storedPw = sessionStorage.getItem('pw');
      const storedEmail = sessionStorage.getItem('email');
      const storedTel = sessionStorage.getItem('tel');

      if (storedLoginId) setLoginId(storedLoginId);
      if (storedPw) setPw(storedPw);
      if (storedEmail) setEmail(storedEmail);
      if (storedTel) setTel(storedTel);
    }, []);

    const handleSubmit = (event) => {
      event.preventDefault();
      console.log(' :', { loginId, pw, email, tel });
    };

    const getDisplayPassword = () => {
      if (pw.length === 0) {
        return pw;
      }
      return pw[0] + '*'.repeat(pw.length - 1);
    };

    const handleDelete = async () => {
      const userNo = sessionStorage.getItem('userNo');
      console.log('Retrieved userNo from sessionStorage:', userNo);

      if (!userNo) {
        console.error('  .');
        return;
      }

      try {
        const response = await fetch(`/api/users/delete/${userNo}`, {
          method: 'DELETE',
        });

        if (response.ok) {
          console.log(' :', { userNo });
          sessionStorage.clear();
          window.location.href = '/';
        } else {
          console.error(' ');
        }
      } catch (error) {
        console.error('    :', error);
      }
    };
  //   
  const handleUpdate = async () => {
      const userNo = sessionStorage.getItem('userNo');
      console.log('Retrieved userNo from sessionStorage:', userNo);

      if (!userNo) {
          console.error('  .');
          return;
      }

      const updatedUser = {
          userNo,
          loginId,
          pw,
          email,
          tel
      };

      try {
          const response = await fetch('/api/users/update', {
              method: 'POST',
              headers: {
                  'Content-Type': 'application/json'
              },
              body: JSON.stringify(updatedUser)
          });

          if (response.ok) {
              console.log('  .');
              //    
          } else {
              console.error('   ');
          }
      } catch (error) {
          console.error('      :', error);
      }
  };

    return (
      <div className="mypage-container">
        <h2> </h2>
        <Link to="/mypage"> </Link>
        <form onSubmit={handleSubmit}>
          <div className="input-group">
            <label htmlFor="loginId">:</label>
            <input
              type="text"
              id="loginId"
              value={loginId}
              onChange={(e) => setLoginId(e.target.value)}
              required
            />
          </div>
          <div className="input-group">
            <label htmlFor="pw">:</label>
            <input
              type="text"
              id="pw"
              value={getDisplayPassword()}
              onChange={(e) => setPw(e.target.value)}
              required
            />
          </div>
          <div className="input-group">
            <label htmlFor="email">:</label>
            <input
              type="email"
              id="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              required
            />
          </div>
          <div className="input-group">
            <label htmlFor="tel">:</label>
            <input
              type="tel"
              id="tel"
              value={tel}
              onChange={(e) => setTel(e.target.value)}
              required
            />
          </div>
          <button type="submit" onClick={handleUpdate}></button>
          <button type="button" onClick={handleDelete}></button>
        </form>
      </div>
    );
  }
export default MyPlan;
