import React, { useState, useEffect } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import axios from 'axios';

function MyPage() {
  const [plans, setPlans] = useState([]);
  const navigate = useNavigate();

  useEffect(() => {
    const fetchPlanData = async () => {
      try {
        const response = await axios.get('/api/users/saves');
        const formattedPlans = response.data.map(plan => ({
          ...plan,
          firstDate: plan.firstDate.slice(0, 10),
          endDate: plan.endDate.slice(0, 10)
        }));
        setPlans(formattedPlans);
      } catch (error) {
        console.error('Error fetching plan data:', error);
      }
    };

    fetchPlanData();
  }, [plans]);

  const handleShowDetails = (plan) => {
    navigate('/MyPlanDetail', { state: { plan } });
  };

  const handleDeletePlan = async (saveNo) => {
    try {
      await axios.delete(`/api/users/${saveNo}/delete`);
      //       UI .
      setPlans(plans.filter(plan => plan.saveNO !== saveNo));
    } catch (error) {
      console.error('    :', error);
    }
  };

  return (
    <div className="myplan-container">
      <h2></h2>

      <Link to="/myplan"></Link>
      {plans.length > 0 ? (
        <div className="plan-list">
         {plans.map((plan, index) => (
           <div key={index} className="plan-details">
             <p><strong> :</strong> {plan.saveTitle}</p>
             <p><strong>  :</strong> {plan.firstDate}</p>
             <p><strong>  :</strong> {plan.endDate}</p>
             <p><strong>:</strong> {plan.firstPlace}</p>
             <p><strong>:</strong> {plan.endPlace}</p>

             <button type="button" onClick={() => handleShowDetails(plan)}> </button>
             <button type="button" onClick={() => handleDeletePlan(plan.saveNo)}></button>
           </div>
         ))}
        </div>
      ) : (
        <p>  .</p>
      )}
    </div>
  );
}

export default MyPage;
